import { Component } from '@angular/core';
import { Inject, Injectable } from '@angular/core';
import { ApiService } from '../../api.service';
import { EventsService } from '../../events.service';
import { LocalstorageService } from '../../localstorage.service';
import { Router } from '@angular/router';
import { LOCAL_STORAGE, StorageService } from 'ngx-webstorage-service';

@Component({
  selector: 'app-dashboard',
  templateUrl: 'login.component.html'
})
export class LoginComponent {
	adm_username: any = '';
	adm_password: any = '';
	adm: any = [];
	showAlert: any = false;
 
	constructor(
    
  	public api: ApiService,
  	public router: Router,
    public event: EventsService,
    public localstorage: LocalstorageService
  	) { }

  ngOnInit() {
  	
  }

  getLogin(){
  	this.api.provideData({
      type: 'getloginadm',
      adm_username: this.adm_username,
      adm_password: this.adm_password
    },'data-adm-api').subscribe((resp)=>{
      this.adm = resp;
      console.log(this.adm);
      if(this.adm.length>0){
        this.event.publish('user:login', {
          user: this.adm,
          time: new Date()
      });

        this.localstorage.storeOnLocalStorage(this.adm);
      	this.router.navigateByUrl('/dashboard');
      }else{
      	this.showAlert = true;
      	setTimeout(()=>{                    
		   this.showAlert = false;
		 }, 3000);
      }
      
    });
  }

  

 }
