import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// Import Containers
import { DefaultLayoutComponent } from './containers';

import { P404Component } from './views/error/404.component';
import { P500Component } from './views/error/500.component';
import { LoginComponent } from './views/login/login.component';
import { RegisterComponent } from './views/register/register.component';

export const routes: Routes = [
  {
    path: '',
    redirectTo: 'login',
    pathMatch: 'full',
  },
  {
    path: '404',
    component: P404Component,
    data: {
      title: 'Page 404'
    }
  },
  {
    path: '500',
    component: P500Component,
    data: {
      title: 'Page 500'
    }
  },
  {
    path: 'login',
    component: LoginComponent,
    data: {
      title: 'Login Page'
    }
  },
  {
    path: 'register',
    component: RegisterComponent,
    data: {
      title: 'Register Page'
    }
  },
  {
    path: '',
    component: DefaultLayoutComponent,
    data: {
      title: 'Home'
    },
    children: [
      {
        path: 'base',
        loadChildren: () => import('./views/base/base.module').then(m => m.BaseModule)
      },
      {
        path: 'buttons',
        loadChildren: () => import('./views/buttons/buttons.module').then(m => m.ButtonsModule)
      },
      {
        path: 'charts',
        loadChildren: () => import('./views/chartjs/chartjs.module').then(m => m.ChartJSModule)
      },
      {
        path: 'dashboard',
        loadChildren: () => import('./views/dashboard/dashboard.module').then(m => m.DashboardModule)
      },
      {
        path: 'icons',
        loadChildren: () => import('./views/icons/icons.module').then(m => m.IconsModule)
      },
      {
        path: 'notifications',
        loadChildren: () => import('./views/notifications/notifications.module').then(m => m.NotificationsModule)
      },
      {
        path: 'theme',
        loadChildren: () => import('./views/theme/theme.module').then(m => m.ThemeModule)
      },
      {
        path: 'widgets',
        loadChildren: () => import('./views/widgets/widgets.module').then(m => m.WidgetsModule)
      },
      {
        path: 'dmp-list',
        loadChildren: () => import('./views/dmp-list/dmp-list.module').then(m => m.DmpListModule)
      },
      {
        path: 'dmp-edit',
        loadChildren: () => import('./views/dmp-edit/dmp-edit.module').then(m => m.DmpEditModule)
      },
      {
        path: 'add-dmp',
        loadChildren: () => import('./views/add-dmp/add-dmp.module').then(m => m.AddDmpModule)
      },
      {
        path: 'news-category',
        loadChildren: () => import('./views/news-category/news-category.module').then(m => m.NewsCategoryModule)
      },
      {
        path: 'radio-program',
        loadChildren: () => import('./views/radio-program/radio-program.module').then(m => m.RadioProgramModule)
      },
      {
        path: 'radio-programadd',
        loadChildren: () => import('./views/radio-programadd/radio-programadd.module').then(m => m.RadioProgramaddModule)
      },
      {
        path: 'radio-programedit',
        loadChildren: () => import('./views/radio-programedit/radio-programedit.module').then(m => m.RadioProgrameditModule)
      },
      {
        path: 'radio-announcer',
        loadChildren: () => import('./views/radio-announcer/radio-announcer.module').then(m => m.RadioAnnouncerModule)
      },
      {
        path: 'radio-config',
        loadChildren: () => import('./views/radio-config/radio-config.module').then(m => m.RadioConfigModule)
      },
      {
        path: 'radio-cityguider',
        loadChildren: () => import('./views/radio-cityguider/radio-cityguider.module').then(m => m.RadioCityguiderModule)
      },
      {
        path: 'app-config',
        loadChildren: () => import('./views/app-config/app-config.module').then(m => m.AppConfigModule)
      },
      {
        path: 'app-banner',
        loadChildren: () => import('./views/app-banner/app-banner.module').then(m => m.AppBannerModule)
      },
      {
        path: 'app-bannerradio',
        loadChildren: () => import('./views/app-bannerradio/app-banner.module').then(m => m.AppBannerModule)
      },
      {
        path: 'tv',
        loadChildren: () => import('./views/tv/tv.module').then(m => m.TvModule)
      }
    ]
  },
  { path: '**', component: P404Component }
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}
