import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';
import { HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ApiService {
	// url: any = 'http://localhost/projectKantorpos/public/api/';
	// url: any = 'https://aminku.id/project/projectKantorpos/api/';
	url= "https://ameg.id/api/";
  uploadUrl= "https://ameg.id/api/";
	authState = new BehaviorSubject(false);
	httpOptions: any;
  resp: any;
  constructor(public http: HttpClient) {
  	this.httpOptions = {
	  headers: new HttpHeaders({
	    'Content-Type':  'application/json'
	  })
	};
  }

    provideRestWP(url){
        return this.http.get(url, this.httpOptions);
    }

    provideDataGet(url){

        return this.http.get(this.url+url, this.httpOptions);

    }

    provideData(data, link){
    	console.log(JSON.stringify(data));
        return this.http.post(this.url+link+".php", JSON.stringify(data), this.httpOptions);

    }
    provideDataForm(data, link){
      console.log(data);
        return this.http.post(this.url+link+".php", data, this.httpOptions);

    }

    refreshData(){
		 this.authState.next(true);
		
    }

}
